import {createApp} from 'vue'
import App from './App.vue'
import {createRouter, createWebHashHistory} from "vue-router";
import {library} from '@fortawesome/fontawesome-svg-core'
import {FontAwesomeIcon} from '@fortawesome/vue-fontawesome'
import Homepage from "@/pages/homePage.vue";
import About from "@/pages/aboutPage.vue";
import Contact from "@/pages/contactPage.vue";
import Experience from "@/pages/experiencePage.vue";
import NotFound from "@/pages/notFound.vue";

import VueHelmet from "vue-helmet/src/VueHelmet";

const routes = [
    {
        path: '/',
        name: 'home',
        component: Homepage
    },
    {
        path: '/about',
        name: 'about',
        component: About
    },
    {
        path: '/experience',
        name: 'experience',
        component: Experience
    },
    {
        path: '/contact',
        name: 'contact',
        component: Contact
    },
    {
        path: '/:pathMatch(.*)*',
        name: 'not found',
        component: NotFound
    }
]
const router = createRouter({
    history: createWebHashHistory(),
    routes
});
import {faXTwitter, faGithub, faInstagram} from '@fortawesome/free-brands-svg-icons'
import {faMailBulk, faFilePdf, faFaceSadTear} from '@fortawesome/free-solid-svg-icons'


/* add icons to the library */
library.add(faXTwitter, faGithub, faInstagram, faMailBulk, faFilePdf,faFaceSadTear)
const app = createApp(App);
app.use(router);
app.component('font-awesome-icon', FontAwesomeIcon)
app.component('vue-helmet', VueHelmet)
app.mount('#app');
