<script setup>
import {computed, onMounted, onUnmounted, ref} from "vue";
import INFO from "../../data/user";

const MIN_LOGO_SIZE = 40;
const MAX_LOGO_SIZE = 80;
const LOGO_CHANGE_RATE = 2.5;

const logoSize = ref(MAX_LOGO_SIZE);
const logoStay = ref(false);
const scrollY = ref(0);

const handleScroll = () => {
  scrollY.value = window.scrollY;
  const newLogoSize = Math.max(MIN_LOGO_SIZE, MAX_LOGO_SIZE - scrollY.value / LOGO_CHANGE_RATE);
  logoSize.value = newLogoSize;
  logoStay.value = newLogoSize <= MIN_LOGO_SIZE;
};
onMounted(() => {
  window.scrollTo(0, 0);
})

onMounted(() => {
  window.addEventListener("scroll", handleScroll);
});

onUnmounted(() => {
  window.removeEventListener("scroll", handleScroll);
});

const logoStyle = computed(() => {
  return {
    display: "flex",
    position: logoStay.value ? "fixed" : "relative",
    top: logoStay.value ? "3vh" : "auto",
    zIndex: 999,
    border: logoStay.value ? "1px solid var(--link-color)" : "none",
    borderRadius: logoStay.value ? "50%" : "none",
    boxShadow: logoStay.value ? "0px 4px 10px rgba(0, 0, 0, 0.25)" : "none",
  };
});
</script>

<template>
  <div :style="logoStyle">
    <router-link to="/">
      <img :src="INFO.main.logo" alt="logo" class="logo" :width="logoSize"/>
    </router-link>
  </div>
</template>

<style scoped>
@import "./styles/logo.css";
</style>
